.home-page-container{
    display: flex;
    flex-direction: column;
    .resume-holder{
        display: flex;
        flex-direction: column;
        align-items: center;
        .contact-me-holder{
            display: flex;
            flex-direction: row;
            background-color: #FFFFFF;
            border-radius: 15px;
            box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
            width: 100%;
            .profile-image-holder{
                padding: 10px;
                height: 100px;
                .avatar-placeholder{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 80px;
                    height: 80px;
                    background-color: $primary;
                    border-radius: 15px;
                    color: $text-gray;
                    font-size: 40px;
                }
                img{
                    border-radius: 15px;
                }
            }
            .data-holder{
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                padding: 10px;
                width: 100%;
                .name-holder{
                    display: flex;
                    flex-direction: column;
                    .name{
                        font-size: 18px;
                        user-select: none;
                    }
                    .current-position{
                        font-size: 12px;
                        color: $subtitle-gray;
                        font-weight: normal;
                        padding-top: 3px;
                        user-select: none;
                    }
                }
                .contact-items-list{
                    margin-top: 5px;
                    .contact-item-holder{
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        margin-bottom: 6px;
                        .icon{
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            padding-right: 10px;
                        }
                        .value{
                            font-size: 13px;
                            font-weight: 300;
                            color: $text-gray;
                            user-select: none;
                        }
                    }
                }
                .action-buttons{
                    display: flex;
                    flex-direction: row;
                }
            }
        }
        .about-me-holder{
            display: flex;
            flex-direction: column;
            background-color: #FFFFFF;
            box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
            width: 100%;
            border-radius: 15px;
            margin-top: 5px;
            .title-holder{
                display: flex;
                padding: 10px 0px 0px 10px;
                color: $subtitle-gray;
            }
            .about-me{
                display: flex;
                padding: 0px 10px 10px 10px;
                font-family: "Biennale";
                font-weight: normal;
                font-size: 12px;
                text-align: justify;
                color: $subtitle-gray;
            }
        }
        .experiences-holder{
            display: flex;
            flex-direction: column;
            background-color: #FFFFFF;
            border-radius: 10px;
            margin-top : 5px;
            .title{
                padding: 10px 0px 0px 10px;
                color: $subtitle-gray;
            }
            .body{
                display: flex;
                flex-direction: column;
                border-radius: 10px;
                padding: 10px;
                .experience-item-holder{
                    .header{
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        .data{
                            .company{
                                font-size: 13px;
                                font-weight: 500;
                                margin-right: 2px;
                                color: $subtitle-gray;
                                padding: 3px;
                                background-color: $primary;
                                border-radius: 5px;
                                width: 30%;
                            }
                            .job-title{
                                margin-right: 2px;
                                color: $subtitle-gray;
                                font-size: 12px;
                            }
                            .company-title{
                                font-size: 10px;
                                font-weight: 300;
                                margin-right: 2px;
                                margin-top: 2px;
                                color: $text-gray;
                            }
                            .date{
                                font-size: 10px;
                                font-weight: 300;
                                margin-left: 2px;
                                color: $text-gray;
                            }
                        }
                    }
                    .description{
                        font-weight: 300;
                        font-size: 12px;
                        color: $text-gray;
                        text-align: justify;
                    }
                    .actions{
                        padding: 0px 2px 2px 2px;
                        img{
                            margin-right: 5px;
                        }
                        .action-holder{
                            display: flex;
                            flex-direction: row;
                            align-items: center;
                            color: $text-gray;
                            font-weight: 400;
                            font-size: 11px;
                            padding: 3px 0px 3px 0px;
                        }
                    }
                }
                .seperator{
                    width: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    color:$subtitle-gray;
                    margin-top: -10px;
                }
            }
        }
